import {Component, Input, QueryList, ContentChildren, Output, EventEmitter} from '@angular/core';
import {isInt, toBoolean} from '../util/util';
import {NglTab} from './tab';


export class NglTabs {
   type: 'default' | 'scoped' = 'default';

   tabs: QueryList<NglTab>;

  activeTab: NglTab;
  selected: string | number | NglTab;
   set setSelected(selected: string | number | NglTab) {
    if (selected === this.selected) return;

    this.selected = selected;

    if (!this.tabs) return; // Wait for content to initialize

    this.activate();
  }

   selectedChange = new EventEmitter<NglTab>();

   set titleCaps(titleCaps: any) {
    this._titleCaps = toBoolean(titleCaps);
  }
  get titleCaps() {
    return this._titleCaps;
  }

  private _titleCaps = true;

  ngAfterContentInit() {
    // Initial selection after all tabs are created
    this.activate();
    if (!this.activeTab) {
      setTimeout(() => this.select(this.tabs.first));
    }
  }

  select(tab: NglTab) {
    this.selectedChange.emit(tab);
  }

  move(evt: Event, moves: number) {
    evt.preventDefault();

    const tabs = this.tabs.toArray();
    const selectedIndex = tabs.indexOf( this.activeTab );
    this.select( tabs[(tabs.length + selectedIndex + moves) % tabs.length] );
  }

  private activate() {
    if (this.activeTab) {
      this.activeTab.active = false;
    }
    this.activeTab = this.findTab();
    if (this.activeTab) {
      this.activeTab.active = true;
    }
  }

  private findTab(value: any = this.selected): NglTab {
    if (value instanceof NglTab) {
      return value;
    }
    if (isInt(value)) {
      return this.tabs.toArray()[+value];
    }
    return this.tabs.toArray().find((t: NglTab) => {
      return t.id && t.id === value;
    });
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'ngl-tabs',
  template: `
    <ul [ngClass]="'slds-tabs--' + type + '__nav'" role="tablist" (keydown.ArrowLeft)="move($event, -1)" (keydown.ArrowRight)="move($event, 1)"><li *ngFor="let tab of tabs" [ngClass]="'slds-tabs--' + type + '__item'" [class.slds-active]="tab.active" [class.slds-text-title--caps]="titleCaps" (click)="select(tab)" role="presentation"><a [nglInternalOutlet]="tab.heading" [ngClass]="'slds-tabs--' + type + '__link'" role="tab" [attr.aria-selected]="tab.active" [attr.tabindex]="tab.active ? 0 : 1"></a></li></ul><div [ngClass]="'slds-tabs--' + type + '__content'" role="tabpanel"><ng-template [ngTemplateOutlet]="activeTab?.templateRef"></ng-template></div>
  `,
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'type': [{ type: Input },],
'tabs': [{ type: ContentChildren, args: [NglTab, ] },],
'setSelected': [{ type: Input, args: ['selected', ] },],
'selectedChange': [{ type: Output },],
'titleCaps': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
